import { forwardRef, useEffect } from "react";
import { width, height, isCreateAllow } from "./Physic";

function Screen({ circles, planCircle, mouseOnScreen, handlePress }, ref) {
  useEffect(() => {
    if (mouseOnScreen) {
      if (isCreateAllow(planCircle, circles))
        document.body.style.cursor = "crosshair";
      else document.body.style.cursor = "not-allowed";
    }
  }, [circles, mouseOnScreen, planCircle]);

  return (
    <div
      ref={ref}
      className="collision-box"
      style={{ width, height }}
      onMouseDown={handlePress}
    >
      {circles.map((circle, index) => (
        <div
          className="collision-ball"
          key={index}
          style={{
            top: `${circle.y}px`,
            left: `${circle.x}px`,
            height: `${circle.r * 2}px`,
            width: `${circle.r * 2}px`,
            borderRadius: `${circle.r}px`,
          }}
        />
      ))}
      <div
        id="create"
        className="collision-preview"
        style={{
          display: mouseOnScreen ? "block" : "none",
          height: `${planCircle.r * 2}px`,
          width: `${planCircle.r * 2}px`,
          borderRadius: `${planCircle.r}px`,
          transform: `translate3d(${planCircle.x}px, ${planCircle.y}px, 0)`,
        }}
      />
    </div>
  );
}

export default forwardRef(Screen);
