import { useEffect, useRef, useState } from "react";
import Screen from "./Screen";
import {
  sample,
  updateCircles,
  isCreateAllow,
  isInsideScreen,
  getKE,
} from "./Physic";
import {
  Box,
  Checkbox,
  FormControlLabel,
  Slider,
  Typography,
} from "@mui/material";
import ControlBar from "./ControlBar";
import useDocumentTitle from "../../hooks/useDocumentTitle";

const render_time = 10;

const radius_marks = [...Array(7).keys()].map((i) => ({
  value: i * 10 + 20,
  label: i * 10 + 20,
}));

const speed_marks = [...Array(11).keys()].map((i) => ({
  value: i - 5,
  label: i - 5,
}));

const round1decimal = (value) => Math.round(value * 10) / 10;

function Collision() {
  const screen_ref = useRef();
  const [mouseOnScreen, setMouseOnScreen] = useState(false);
  const [circles, setCircles] = useState(sample);
  const [timer, setTimer] = useState();
  const [planCircle, setPlanCircle] = useState({
    x: 0,
    y: 0,
    r: 50,
    dx: 0,
    dy: 0,
  });
  const [play, setPlay] = useState(false);
  const friction = useRef(false);
  useDocumentTitle("Collision Simulator");

  function updateState() {
    setCircles((prev) => updateCircles(prev, friction.current));
  }

  function handleEnter() {
    setMouseOnScreen(true);
  }

  function handleLeave() {
    document.body.style.cursor = "auto";
    setMouseOnScreen(false);
  }

  function handlePress() {
    if (isCreateAllow(planCircle, circles)) {
      setCircles([
        ...circles,
        { ...planCircle, Fx: 0, Fy: 0, mass: planCircle.r / 10 },
      ]);
    }
  }

  function updateCursorPosition(event) {
    const rect = screen_ref.current.getBoundingClientRect();
    const x = event.clientX - rect.left;
    const y = event.clientY - rect.top;
    setPlanCircle((prev) => ({
      ...prev,
      x: x - planCircle.r,
      y: y - planCircle.r,
    }));
    if (isInsideScreen(x, y)) handleEnter();
    else handleLeave();
  }

  const handleClean = () => {
    setCircles([]);
  };

  const handlePlay = () => {
    setTimer(setInterval(updateState, render_time));
    setPlay(true);
  };

  const handlePause = () => {
    clearInterval(timer);
    setPlay(false);
  };

  useEffect(() => {
    window.addEventListener("mousemove", updateCursorPosition);

    return () => {
      clearInterval(timer);
      window.removeEventListener("mousemove", updateCursorPosition);
    };
    // eslint-disable-next-line
  }, []);

  return (
    <Box p={3}>
      <Screen
        circles={circles}
        planCircle={planCircle}
        mouseOnScreen={mouseOnScreen}
        handlePress={handlePress}
        ref={screen_ref}
      />
      <Box width={400}>
        <ControlBar
          play={play}
          handleClean={handleClean}
          handlePause={handlePause}
          handlePlay={handlePlay}
        />
        <Typography>
          Total Kinetic Energy: {round1decimal(getKE(circles))}J
        </Typography>
        <FormControlLabel
          control={
            <Checkbox
              onChange={(e) => {
                friction.current = e.target.checked;
              }}
            />
          }
          label="Friction"
        />
        <Typography mt={1}>Radius:</Typography>
        <Slider
          value={planCircle.r}
          onChange={(e) => setPlanCircle({ ...planCircle, r: e.target.value })}
          step={5}
          min={20}
          max={80}
          valueLabelDisplay="auto"
          sx={{ ml: 1, mr: 1 }}
          marks={radius_marks}
        />
        <Typography mt={1}>Horizontal Speed:</Typography>
        <Slider
          value={planCircle.dx}
          onChange={(e) => setPlanCircle({ ...planCircle, dx: e.target.value })}
          step={1}
          min={-5}
          max={5}
          valueLabelDisplay="auto"
          sx={{ ml: 1, mr: 1 }}
          marks={speed_marks}
        />
        <Typography mt={1}>Vertical Speed:</Typography>
        <Slider
          value={planCircle.dy}
          onChange={(e) => setPlanCircle({ ...planCircle, dy: e.target.value })}
          step={1}
          min={-5}
          max={5}
          valueLabelDisplay="auto"
          sx={{ ml: 1, mr: 1 }}
          marks={speed_marks}
        />
        <Typography mt={1}>Mass: {planCircle.r / 10} kg</Typography>
        <Typography mt={1}>
          Kinetic Energt:{" "}
          {round1decimal(getKE([{ ...planCircle, mass: planCircle.r / 10 }]))} J
        </Typography>
        <div
          style={{
            height: `${planCircle.r * 2}px`,
            width: `${planCircle.r * 2}px`,
            borderRadius: `${planCircle.r}px`,
            backgroundColor: "rebeccapurple",
            marginTop: "10px",
          }}
        ></div>
      </Box>
    </Box>
  );
}

export default Collision;
